.main_block {
    height: 12rem;
    margin: 10px;
    padding: 10px 20px;
}

.image_block {
    display: grid;
    grid-template-columns: 1fr;
    /* width: 10rem; */
    /* height: 10rem; */
}

.main_image {
    display: grid;
    justify-self: center;
    width: 15rem;
    height: 10rem;
}

.arrow_image {
    cursor: pointer;
    width: 30px;
    height: 30px;
}

.arrow_image_right {
    position: absolute;
    top: 70px;
    right: 0;
}


.arrow_image_left {
    position: absolute;
    top: 70px;
    left: 0;
}