.main_block {
    display: grid;
    width: 100%;
    height: 100%;
    border: none;
    background-color: #D7D8D5;
}

.content_block {
    position: relative;
    top: 0;
    left: 0;
    width: 35rem;
    height: 35rem;
    border-radius: 10px;
    display: grid;
    justify-self: center;
    align-self: center;
    background-color: gray;
}

.button_block {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-items: center;
    align-items: center;
}

.button_block button {
    color: #fff;
    height: 4rem;
    transition: 0.5s;
    font-size: 20px;
    padding: 10px 20px;
    border-radius: 10px;
}

.exit_image {
    position: absolute;
    width: 20px;
    height: 20px;
    top: 4px;
    right: 6px;
    cursor: pointer;
    transition: 0.5s;
}

.exit_image:hover {
    transform: rotate(360deg);
}

.button_block button:hover {
    transform: scale(1.1);
}

.enable_button {
    background-color: green;
}

.disable_button {
    background-color: red;
}