.main_block {
    display: grid;
}

.content_block {
    display: inline-block;
    background-color: #fff;
    width: 100%;
    height: 100vh;
    padding: 20px 30px;
}

.content_header_block {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    justify-items: center;
    width: 100%;
    background-color: #FBBF24;
    font-weight: bold;
    border-radius: 5px;
    align-items: center;
    height: 3rem;
    position: sticky;
    left: 0;
    top: 0;
}

.estates_block {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    justify-items: center;
    align-items: center;
    text-align: center;
    width: 100%;
    margin-top: 20px;
    border-bottom: 2px solid #000;
    background-color: #fff;
}

.estates_block div {
    margin: 30px 0;
}

.button {
    border: 2px solid #000;
    cursor: pointer;
}